'use strict';

import iotClient from "./client.js";

export default class iot {
    constructor() {
        this.client = new iotClient();
    }

    getFireAlarmDeviceData(pk, dn) {
        //获取设备属性值
        return this.client.queryDevicePropertiesData({
            ProductKey: pk,
            DeviceName: dn,
            "Identifier.1": "CurrentTemperature",
            "Identifier.2": "SmokeSensorState",
            "Identifier.3": "PowerSwitch",
            StartTime: Date.now(),
            EndTime: Date.now() - 60000,
            PageSize: 1,
            Asc: 0
        })
            .then(response => {
                if (response == undefined) return null;

                // console.info(response);
                var data = response.data;
                var ret = {
                    temperature: 0,
                    smokeAlarm: false,
                    switch: false
                };

                if (data.Success) {
                    data.PropertyDataInfos.PropertyDataInfo.forEach((v, i) => {
                        switch (v.Identifier) {
                            case "CurrentTemperature":
                                ret.temperature = parseFloat(
                                    v.List.PropertyInfo[0].Value
                                );
                                break;
                            case "SmokeSensorState":
                                ret.smokeAlarm =
                                    true == v.List.PropertyInfo[0].Value;
                                break;
                            case "PowerSwitch":
                                ret.switch = true == v.List.PropertyInfo[0].Value;
                                break;
                            default:
                                break;
                        }
                    });
                }

                return ret;
            })
            .catch(err => {
                console.error(err);
            });

    }

    getDeviceStatus(pk, dn) {
        //获取设备状态
        return this.client.getDeviceStatus({
            ProductKey: pk,
            DeviceName: dn
        }).then(response => {
            if (response.data.Success)
                return response.data.Data.Status;
            else
                return 'Error';
        }).catch(err => {
            console.error(err);
        });

    }

    setSwitch(pk, dn, sw) {
        var payload = {
            id: Math.floor(Math.random() * (100000 - 100 + 1) + 100),
            version: "1.0",
            params: { PowerSwitch: sw },
            method: "thing.service.property.set"
        };

        return this.client.pub({
            ProductKey: pk,
            TopicFullName: `/sys/${pk}/${dn}/thing/service/property/set`,
            MessageContent: new Buffer(JSON.stringify(payload)).toString("base64")
        });
    }
}  