<template>
  <el-container>
    <el-header>
      <el-row>
        <el-col :span="12" class="col-info">
          <div>&nbsp;&nbsp;</div>
        </el-col>
        <el-col :span="12">
          <el-checkbox v-model="autoRefresh">自动刷新</el-checkbox>
          <span>&nbsp;&nbsp;</span>
          <el-button
            type="primary"
            icon="el-icon-refresh"
            size="mini"
            round
            v-on:click="onRefresh"
          >刷新</el-button>
        </el-col>
      </el-row>
    </el-header>

    <el-main>
      <table>
        <tr v-for="(rowItem, i) in deviceList" :key="i" class="row">
          <td v-for="(cowItem, j) in rowItem" :key="j" class="col">
            <FAunit :deviceData="cowItem"/>
          </td>
        </tr>
      </table>
    </el-main>
  </el-container>
</template>

<script>
import FAunit from "@/components/FireAlarmApp/FireAlarmUnit.vue";
import Iot from "../../lib/iot/iot.js";
import Fc from "../../lib/fc";

var iot = new Iot();

export default {
  beforeMount() {
    for (var i = 0; i < 5; i++) {
      var r = [];
      for (var j = 0; j < 8; j++) {
        var device = {
          productKey: "a1zTwoqIdfD",
          deviceName:
            "fa_" + (10000 + i * 8 + j + 1).toString().substring(1, 5),
          data: {
            online: false,
            temperature: 20 + Math.random(),
            smokeAlarm: true,
            switch: false
          }
        };

        r.push(device);
      }

      this.deviceList.push(r);
    }

    // console.log(this.deviceList);

    this.timer = setInterval(() => {
      if (this.autoRefresh) this.refresh();
    }, 1000);
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
  data() {
    return {
      deviceList: [],
      // 当前选中的设备
      deviceName: "",
      productKey: "",

      // 设置值
      setSwitch: true,

      autoRefresh: false
    };
  },
  components: {
    FAunit
  },
  methods: {
    refresh() {
      Fc.getFloorCurrentData("4_3").then(ret => {
        console.info("refresh: %j", ret.data);

        for (var i = 0; i < this.deviceList.length; i++) {
          var r = this.deviceList[i];
          for (var j = 0; j < r.length; j++) {
            var device = r[j];

            var data = ret.data.filter((value, index) => {
              return value.deviceName == device.deviceName;
            });

            if (data.length > 0) {
              device.data.temperature = data[0].CurrentTemperature;
              device.data.smokeAlarm = data[0].SmokeSensorState;
              device.data.switch = data[0].PowerSwitch;

              this.$set(r, j, device);
            }
          }
        }
      });
    },
    onRefresh() {
      this.refresh();
    }
  }
};
</script>

