'use strict';

import axios from 'axios';

export function a1() {
    return 'a1';
}

export function callFc(url, params) {
    return axios({
        method: 'get',
        url: url,
        // headers: { 'Date': '2019-01-07' },   // 前端不需要设置此参数，但是后端需要
        params: params
    })
        .catch(function (error) {
            if (error.response) {
                // The request was made and the server responded with a status code
                // that falls out of the range of 2xx
                console.error('response data: ', error.response.data);
                console.error('response status: ', error.response.status, error.response.statusText);
                console.error('response headers: ', error.response.headers);
            } else if (error.request) {
                // The request was made but no response was received
                // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
                // http.ClientRequest in node.js
                console.error('request: ', error.request);
            } else {
                // Something happened in setting up the request that triggered an Error
                console.error('Error: ', error.message);
            }
            console.error(error);
        });
}

export function getFloorCurrentData(fid) {
    var url = 'https://1881220991419025.cn-shanghai.fc.aliyuncs.com/2016-08-15/proxy/FireAlarm/getCurrentData/' + fid;
    // var url = 'http://api.yiiot.top/currentData/' + fid;

    return callFc(url, {}).then(response => {
        if (response == undefined) return null;

        // console.info('getFloorCurrentData: ', response);
        if (response.status == 200) {
            response.data.data.forEach((v, i) => {
                v.CurrentTemperature = parseFloat(
                    v.CurrentTemperature
                );
                v.SmokeSensorState = parseFloat(
                    v.SmokeSensorState
                );
                v.PowerSwitch = parseFloat(
                    v.PowerSwitch
                );
            });

            return response.data;
        }
        else
            return null;

    }).catch(err => {
        console.error(err);
    });
}

export default {
    a1, getFloorCurrentData
}